import request from '@/utils/request'
export function userInfo(params) {
  return request({
    url: 'api/user/userInfo',
    method: 'get',
    params
  })
}
export function recognitionIdcard(params) {
  return request({
    url: 'api/user/recognitionIdcard',
    method: 'get',
    params
  })
}
//上传
export function upload(data) {
  return request({
    url: 'api/user/upload',
    method: 'post',
    data
  })
}
export function wechatPay(params) {
  return request({
    url: 'api/wechat/wechatPay',
    method: 'get',
    params
  })
}

export function getMoney(params) {
  return request({
    url: 'api/user/getMoney',
    method: 'get',
    params
  })
}
//修改银行卡信息
export function updateBankCard(data) {
  return request({
    url: 'api/user/updateBankCard',
    method: 'post',
    data
  })
}



