import axios from 'axios'
import {Toast} from 'vant';
import router from '../router'

var url = "";

//判断环境
if (process.env.NODE_ENV === "development") {
  url = '/api/'
} else {
  url = window.location.origin+'/public/index.php/';
}


//创建一个aixios实例
const service = axios.create({
  //自定义默认配置
  baseURL: url,
  withCredentials: true,
  timeout: 5000
});


//在发送请求之前做的事情。
service.interceptors.request.use(
  config => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers['token'] = token;
    }
    return config
  },
  error => {
    return Promise.reject(error)
  }
);


//响应拦截器
service.interceptors.response.use(
  response => {
    const res = response.data;
    if (res.errcode === 0) {
      if (res.msg !== '请求成功~' && res.msg !== '请求成功' && res.msg !== '' && res.msg !== 'success') {

      }
      return res.res
    } else if (res.errcode === 4000) {
      Toast({
        message: res.msg || '参数错误',
        duration: 1000
      });
    } else if (res.errcode == 4001) {
      window.location.href = "https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx2a30238e7bdb6c89&redirect_uri=" + "http://xinan.flcs88.com/public/index.php/api/login/back?" + "&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect";
    } else if (res.errcode === 4002) {
      Toast({
        message: res.msg || '禁用',
        duration: 1000
      });
    } else if (res.errcode === 4003) {
      Toast({
        message: res.msg || '网络错误',
        duration: 1000
      });
    } else if (res.errcode === 5000) {
      Toast({
        message: res.msg || '操作失败',
        duration: 1000
      });
    } else if (res.Code === 0) {
      return res;
    } else {
      Toast({
        message: res.msg,
        duration: 1000
      });
    }
    return Promise.reject()
  },
  error => {
    Toast({
      message: '系统错误',
      duration: 1000
    });
    return Promise.reject(error)
  }
);
export default service



