<template>
  <div class="index">
    <div class="home-gif">
      <img src="../../assets/img/my/bink.gif" alt="">
    </div>
    <div class="button-group">
      <van-button type="default" @click="participate">参与活动</van-button>
      <van-button type="primary" @click="activityRule">活动规则</van-button>
    </div>
    <!--滚动列表-->
    <vue-seamless-scroll :data="listData" :class-option="optionHover" class="seamless-warp">
      <ul class="item">
        <li class="item-list" v-for="item in listData">
          <span class="title" v-text="item.name"></span>
          <span class="title" v-text="item.money"></span>
          <span class="date" v-text="item.content"></span>
        </li>
      </ul>
    </vue-seamless-scroll>
    <div class="fill"></div>
    <!--对话框-->
    <van-popup v-model="show" :style="{ height: '73%',width:'85%' }">
      <img class="cancel" @click="cancel" src="../../assets/img/my/cancel.png" alt="取消">
      <div class="rule">
        <ul class="list">
          <li v-for="(item ,index) in rule" :key="index">{{item.content}}</li>
          <li>活动时间：<br>2020年1月1日—2020年3月31日</li>
        </ul>
      </div>
    </van-popup>
    <van-dialog v-model="binkbank" title="绑定金燕卡" :message="messages" show-cancel-button confirmButtonText="已绑定"
                cancelButtonText="添加银行卡" @confirm="addCard(1)" @cancel="addCard(2)"
                :closeOnClickOverlay="true"></van-dialog>
    <van-dialog v-model="bankShow" title="验证金燕卡" @confirm="confirm" @cancel="participate" :closeOnClickOverlay="true"
                show-cancel-button>
      <van-uploader :after-read="afterRead">
        <img class="camera" src="../../assets/img/my/camera.png" alt="上传">
      </van-uploader>
      <van-field v-model="binkCard" type="text"/>
    </van-dialog>
    <!--红包-->
    <van-popup v-model="redPacket" :style="{ height: '73%',width:'85%' }">
      <img class="cancel" @click="redPacketHide" src="../../assets/img/my/cancel.png" alt="取消">
      <div class="rule" style="text-align: center">
        <div class="congratulation">恭喜您获得！</div>
        <div class="congratulation-money">￥{{money}}元</div>
        <van-button class="btn" @click="redPacketHide">立即领取</van-button>
      </div>
    </van-popup>
  </div>
</template>

<script>
  var thit;
  import {userInfo, recognitionIdcard, upload, wechatPay, getMoney, updateBankCard} from '@/api/user/user'
  import {Dialog} from 'vant';
  import {Notify} from 'vant';
  import {Toast} from 'vant';

  export default {
    name: "index",
    data() {
      return {
        show: false,
        binkbank: false,
        bankShow: false,
        //红包
        redPacket: false,
        binkCard: '',
        messages: `<div>请您确认是否绑定<span style="color: #DE4F53">新安农商银行金燕卡</span></div>`,
        is_bind: '',
        rule: [
          {
            content: '1.微信绑定新安农商银行金燕卡；',
          },
          {
            content: '2.拍摄金燕卡进行验证并核对卡号；',
          },
          {
            content: '3.选择绑定的金燕卡支付1分钱（零钱或者其它银行卡支付将领取不到红包)；'
          },
          {
            content: '4.红包金额随机为1.88元至88.88元；'
          },
          {
            content: '5.每个客户活动期间每月只能领取一次；',
          },
          {
            content: '6.如有问题请咨询新安农商银行各网点。',
          },
        ],
        listData: [{
          'name': '卢先生',
          'content': '获得了红包',
          'money': '1.66元'
        }, {
          'name': '宋先生',
          'content': '获得了红包',
          'money': '1.96元'
        }, {
          'name': '明女士',
          'content': '获得了红包',
          'money': '3.88元'
        }, {
          'name': '李先生',
          'content': '获得了红包',
          'money': '2.66元'
        }, {
          'name': '吴女士',
          'content': '获得了红包',
          'money': '4.66元'
        }, {
          'name': '明女士',
          'content': '获得了红包',
          'money': '3.88元'
        }, {
            'name': '段先生',
            'content': '获得了红包',
            'money': '6.66元'
        }, {
            'name': '洪先生',
            'content': '获得了红包',
            'money': '66.66元'
        }, {
          'name': '李先生',
          'content': '获得了红包',
          'money': '2.66元'
        }, {
          'name': '段先生',
          'content': '获得了红包',
          'money': '6.66元'
        }, {
          'name': '宋先生',
          'content': '获得了红包',
          'money': '1.66元'
        }, {
          'name': '洪先生',
          'content': '获得了红包',
          'money': '66.66元'
        }, {
          'name': '周先生',
          'content': '获得了红包',
          'money': '7.77元'
        }, {
          'name': '王先生',
          'content': '获得了红包',
          'money': '1.11元'
        }, {
          'name': '洪先生',
          'content': '获得了红包',
          'money': '66.66元'
        }, {
          'name': '康先生',
          'content': '获得了红包',
          'money': '3.88元'
        }, {
          'name': '洪先生',
          'content': '获得了红包',
          'money': '66.66元'
        }],
        //金额
        money: '',
        bankMessage: '',
      }
    },
    methods: {
      //获取用户信息
      getUser() {
        userInfo().then((res) => {
          this.is_bind = res.is_bind;
          this.getMoney();
        })
      },
      participate() {
        this.binkbank = true;
        this.binkCard = '';
      },
      //添加银行卡
      addCard(value) {
        if (value === 1) {
          if (this.is_bind === 1) {
            wechatPay().then((resp) => {
              console.log(resp);
              WeixinJSBridge.invoke(
                "getBrandWCPayRequest",
                {
                  appId: resp.appId,
                  timeStamp: resp.timeStamp,
                  nonceStr: resp.nonceStr,
                  package: resp.package,
                  signType: resp.signType,
                  paySign: resp.paySign
                },
                function (resp) {
                  if (resp.err_msg == "get_brand_wcpay_request:ok") {
                    thit.getMoney();
                    thit.binkCard = '';
                  }
                }
              );
            })
          } else {
            this.bankShow = true;
          }
        } else {
          window.location.href = 'https://kf.qq.com/touch/product/weixinpay_app.html';
        }
      },
      //取消
      cancel() {
        this.show = false;
      },
      //规则
      activityRule() {
        this.show = true;
      },
      //文件上传
      afterRead(file) {
        //自定义使用压缩方式
        lrz(file.content, {
          width: 300,
          quality: 0.7
        }).then(function (rst) {
          //处理base64
          let img = rst.base64.split(',')[1];
          let data = new FormData;
          data.append('img', img);
          //上传
          upload(data).then((res) => {
            //验证银行卡
            recognitionIdcard({
              path: res.img,
            }).then((res) => {
              thit.bankMessage = res;
              console.log(res.bank_card_number, '2221');
              thit.binkCard = res.bank_card_number.replace(/\s+/g, "");
            })
          })
        }).catch(function (error) {
          //失败时执行
        }).always(function () {
          //不管成功或失败，都会执行
        })
      },
      //验证金燕卡确定
      confirm() {
        updateBankCard({
          bank_number: thit.binkCard,
          bank_card_type: thit.bankMessage.bank_card_type,
          bank_name: thit.bankMessage.bank_name,
        }).then((resp) => {
          console.log(resp);
          //支付接口
          wechatPay().then((res) => {
            console.log(res);
            //微信支付
            WeixinJSBridge.invoke(
              "getBrandWCPayRequest",
              {
                appId: res.appId,
                timeStamp: res.timeStamp,
                nonceStr: res.nonceStr,
                package: res.package,
                signType: res.signType,
                paySign: res.paySign
              },
              function (res) {
                if (res.err_msg == "get_brand_wcpay_request:ok") {
                  Toast("支付成功");
                  thit.getMoney();
                  thit.binkCard = '';
                }
              }
            );
          })
        });
      },
      getMoney() {
        getMoney().then((res) => {
          console.log(res.length);
          if (res.length != 0) {
            thit.redPacket = true;
            thit.money = res.money;
          } else {
            thit.redPacket = false;
          }
        })
      },
      redPacketHide() {
        thit.redPacket = false;
      },
    },
    //计算属性
    computed: {
      optionHover() {
        return {
          hoverStop: false
        }
      }
    },
    created() {
      thit = this;
      this.getUser();
    },
  }
</script>

<style scoped lang="less">
  .index {
    min-height: 100vh;
    background-image: url("../../assets/img/my/bg.png");
    background-repeat: no-repeat;
    background-size: cover;

    .home-gif {
      img {
        width: 100%;
      }
    }

    .button-group {
      display: flex;
      justify-content: space-between;
      margin: 446px 35px 1rem 35px;

      .van-button--default {
        width: 130px;
        height: 32px;
        font-size: 19px;
        line-height: 32px;
        background-color: #DEA10A;
        border: 1px solid #DEA10A;
        border-radius: 15.5px;
        color: #fff;
      }

      .van-button--primary {
        width: 130px;
        height: 32px;
        font-size: 19px;
        line-height: 32px;
        background-color: #7260F7;
        border: 1px solid #7260F7;
        border-radius: 15.5px;
        color: #fff;
      }
    }

    .cancel {
      width: 24px;
      height: 24px;
      margin-left: 274px;
    }

    .rule {
      background-image: url("../../assets/img/my/rule1.png");
      background-size: 100% 92%;
      background-repeat: no-repeat;
      height: 429px;
      padding: 20px 0 0 0;

      .list {
        font-size: 13px;
        list-style: none;
        width: 230px;
        padding-left: 40px;

        li {
          width: 100%;
          margin: 6px;
        }
      }
    }

    .van-popup {
      background-color: rgba(0, 0, 0, 0);
    }
  }

  /deep/ .van-dialog__content {
    text-align: center;

    .camera {
      padding: 19px 0 12px 0;
    }

    .van-cell {
      width: 89%;
      margin: 0 auto;
      background-color: #EEE;
      border-radius: 18px;
      padding: 2px 0.42667rem;
    }
  }

  //滚动列表
  .seamless-warp {
    height: 150px;
    overflow: hidden;
    font-size: 15px;
    color: #fff;

    /deep/ .item-list {
      padding: 0 36px 15px 36px;

      .title {
        display: inline-block;
        width: 32%;
      }

      .date {
        display: inline-block;
        width: 32%;
        text-align: right;
      }
    }
  }

  .fill {
    height: 50px;
  }

  .congratulation {
    padding-top: 40px;
    padding-left: 34px;
    color: red;
    font-size: 26px;
  }

  .congratulation-money {
    color: orangered;
    padding-top: 20px;
    font-size: 22px;
  }

  .btn {
    font-size: 18px;
    height: 40px;
    width: 100px;
    padding: 0 5px;
    line-height: 40px;
    margin: 20px auto 0 auto;
    border: 1px solid yellow;
    background-color: yellow;
    color: darkred;

  }
</style>



